.Easy_container {
  background-color: #bc8cff;
  border: solid 1px black;
  padding-inline: 80px;
}
@media (max-width: 900px) {
  .Easy_container {
    padding-inline: 25px;
  }
}

.card_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  padding-block: 100px;
}
@media (max-width: 900px) {
  .card_container {
    grid-template-columns: repeat(1, 1fr);
  }
}

.card_body {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  background-color: white;
  border: solid 1px black;
  border-radius: 10px;
  box-shadow: 5px 5px 0px 2px rgb(0, 0, 0);
  padding: 35px;
}

.card_img img {
  width: 70px;
  height: 70px;
}

.card_title {
  padding-block-start: 15px;
  padding-block-end: 10px;
}

.card_title h4 {
  font-size: 26px;
}
@media (max-width: 900px) {
  .card_title h4 {
    font-size: 20px;
  }
}

.card_text p {
  font-size: 24px;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
}
@media (max-width: 900px) {
  .card_text p {
    font-size: 18px;
  }
}